/* You can add global styles to this file, and also import other style files */
@import url('https://fonts.googleapis.com/css2?family=Titan+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@600;800&display=swap');

body {
  margin: 0;
  padding: 0;
  background-color: #595959;
}

button {
  border: 2px solid #ffffff;
  background-color: #c8ac87;
  color: #000000;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  margin: 8px;
  font-weight: bolder;
}

nav {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

@page {
  size: A4;
  margin: 10mm;
}

@media print {
  body {
    height: 297mm;
    width: 210mm;
  }

  nav, header {
    display: none;
  }
}
